  font-style: normal;
}

@font-face {
  font-family: "bebas_neue";
  src: url("../fonts/bebasneue_bold.woff2") format("woff2"), url("../fonts/bebasneue_bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
